@import "style/mixins.module.scss";

.root {
    display: flex;
    margin: 0;
}

.theme {
    &_light {
        .label {
            @include color("gray", "0");
        }

        .item-icon {
            @include color("gray", "0");
        }
    }
}

.fieldset {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    width: 100%;
    border: none;
    --webkit-appearence: none;
}

.input {
    width: 100%;
}

.label {
    pointer-events: none;
    @include color("gray", "-4");
    opacity: 0.4;
    position: absolute;
    transform: translate(0) scale(1);

    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 20px;

    z-index: 2;
}

.error-text {
    margin-top: 11px;
    margin-bottom: 0;
    font-size: 13px;
    @include color("primary", "red");
    text-align: left;
    padding-left: 0;
}

.item-icon {
    overflow: hidden;
    border-radius: 50%;
    @include color("gray", "-4");
}
