@import "style/mixins.module.scss";

.root {
    background-color: rgba(238, 243, 244, 0.4);
    position: relative;
    border: none;
    width: 100%;
    cursor: pointer;
    display: block;
    border-radius: 8px;
    padding-left: 16px;
    padding-top: 16px;
    padding-right: 48px;
    padding-bottom: 16px;

    &_checked {
        background-color: get_color("gray", "-5");
    }

    &:last-child {
        margin-bottom: 0;
    }


    &:hover input:not(:checked) ~ .checkmark {
        @include respond-to("mobile") {
            background-color: get_color("primary", "red");
        }

        &::after {
            content: "";
            position: absolute;
            top: 8px;
            left: 8px;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: get_color("gray", "0");

            @include respond-to("laptop") {
                background-color: get_color("gray", "-2");;
            }
        }
    }
}

.radio {
    position: absolute;
    top: 16px;
    right: 16px;
}

.title {
    margin-bottom: 8px;

    @include respond-to("laptop") {
        font-size: 17px;
        line-height: 24px;
    }
}

.text {
    font-size: 17px;
    line-height: 24px;
}

.address {
    margin-bottom: 0;
}