@import "style/mixins.module.scss";

.root {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    margin: 0 0 24px;
    border: none;
    --webkit-appearence: none;

    /* Chrome, Safari, Edge, Opera */
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    /* Firefox */
    input[type="number"] {
        -moz-appearance: textfield;
    }
}

.theme {
    &_light {
        .input {
            @include color("gray", "0");
            border-bottom: 1px solid get_color("opacities", "white-2");

            &::placeholder {
                @include color("gray", "0");
            }

            &:-webkit-autofill,
            &:-webkit-autofill:hover,
            &:-webkit-autofill:focus,
            &:-webkit-autofill:active {
                -webkit-text-fill-color: get_color("gray", "0");
            }

            &_error {
                background-color: get_color("primary", "red");
            }
        }

        .label,
        .error-text {
            @include color("opacities", "white-7");
        }
    }
}

.input {
    width: 100%;
    height: 55px;
    padding: 20px 0 6px 0;
    border: none;
    border-radius: 0;
    background-color: inherit;
    border-bottom: 1px solid rgba(66, 78, 88, 0.1);
    font: inherit;
    font-size: 17px;
    line-height: 24px;
    @include color("gray", "-4");
    outline: none;

    &::placeholder {
        @include color("gray", "-4");
    }

    &:hover {
        border-color: get_color("gray", "-2");
    }

    &:active,
    &:focus {
        border-color: #45a1f8;
    }

    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus,
    &:-webkit-autofill:active {
        transition: background-color 5000s;
        -webkit-text-fill-color: get_color("gray", "-4");
    }

    &:-webkit-autofill::first-line {
        font-size: 17px;
    }

    &_empty {
        padding: 15px 0;
    }

    &_error {
        border-color: get_color("primary", "red");
        background-color: get_color("gray", "0");
        outline-color: get_color("primary", "red");

        &:focus {
            border-color: get_color("primary", "red");
        }

        &:hover {
            background-color: inherit;
        }
    }
}

.label {
    pointer-events: none;
    color: rgba(66, 78, 88, 0.4);
    position: absolute;
    transform: translate(0) scale(1);

    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 20px;

    z-index: 2;
}

.error-text {
    margin-top: 11px;
    font-size: 13px;
    @include color("primary", "red");
    text-align: left;
    padding-left: 0;
}
