@import "style/mixins.module.scss";

.root {
    display: flex;
    flex-direction: column;
    margin: 0 0 16px 0;
    padding: 0;
    border: none;
}

.theme {
    &_light {
        .placeholder {
            @include color("gray", "0");
        }

        .label input[type="checkbox"]:checked ~ span {
            &:before {
                background-color: get_color("gray", "0");
            }

            &:after {
                border: solid get_color("primary", "red");
                border-width: 0 2px 2px 0;
            }
        }

        .error-text {
            @include color("opacities", "white-7");
            -webkit-text-fill-color: get_color("opacities", "white-7");
        }
    }

    &_ukraine {
        .label input[type="checkbox"]:checked ~ span {
            &:before {
                background-color: #ef8463;
            }
        }
    }

    &_germany {
        .label input[type="checkbox"]:checked ~ span {
            &:before {
                background-color: #f0d64c;
            }
            &:after {
                border: solid #36393a;
                border-width: 0 2px 2px 0;
            }
        }
    }
}

.error-text {
    margin-top: 11px;
    font-size: 13px;
    @include color("primary", "red");
    -webkit-text-fill-color: get_color("primary", "red");
    text-align: left;
    padding-left: 0;
}

.placeholder {
    font-size: 17px;
    line-height: 24px;
    font-weight: 400;

    & a {
        @include color("primary", "blue");
    }
}

.label {
    display: flex;
    margin: 0;
    @include color("gray", "-4");
    cursor: pointer;
    user-select: none;
    position: relative;
}

.label input[type="checkbox"] {
    display: none;
}

.label span {
    display: inline-block;
    position: relative;
    padding: 0 0 0 35px;
}

.label input[type="checkbox"] ~ span {
    &:before {
        content: "";
        position: absolute;
        left: 0;
        width: 24px;
        height: 24px;
        background-color: get_color("gray", "-5");
        border-radius: 4px;
    }
}

/* Checked */
.label input[type="checkbox"]:checked ~ span {
    &:before {
        background-color: get_color("primary", "red");
    }

    &:after {
        content: "";
        position: absolute;
        top: 5px;
        left: 9px;
        width: 6px;
        height: 13px;
        border: solid get_color("gray", "0");
        border-width: 0 2px 2px 0;
        transform: rotate(45deg);
    }
}

/* Disabled */
.label input[type="checkbox"]:disabled + span {
    @include color("gray", "-3");
    cursor: default;
}

.label input[type="checkbox"]:disabled + span:before {
    filter: grayscale(100%);
    opacity: 0.6;
}
